<template lang="en">
    <section>
       <div>
            <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{$t('WorkingDay')}}</span>
                    </div>
                </div>
            </div>
        </div> 
    <div class="table-responsive">
      <table class="table table-bordered dt-responsive nowrap mt-0 project_workingweek" id="tblTags" style="width: 100%;">
        <tbody>
          <tr v-for="(item, index) in data" :key="index">
            <td>{{ item.daY_NAME }}</td>
            <td style="display:none">
              {{ item.isWorking ? 1 : 0 }}
            </td>
            <td>
              <div class="form-check form-check-inline">
                <input type="radio" :id="'working_' + item.worK_WEEK_ID + 0" :name="'isWorking'+ index" true-value="true" v-model="item.iS_WORKING" value="true" class="form-control" @change="setTimePickerDisable(1, index)" /> <span class="ml-3">Working</span>
              </div>
            </td>
            <td>
              <div class="form-check form-check-inline">
                <input type="radio" :id="'off_' + item.worK_WEEK_ID + 1" :name="'isWorking'+ index" true-value="false" v-model="item.iS_WORKING" value="false" class="form-control" @change="setTimePickerDisable(0, index)" /> <span class="ml-3">Off</span>
              </div>
            </td>
            <td>
              <span>Timings:</span>
            </td>
            <td>
              <div class="input-group table_timepickerfix" :id="'divShowfromtimepicker_' + index" style="min-width:200px;">
                <!-- Assuming SetTimesPicker is a method of the Vue component -->
                <input type="text" v-model="item.ModifiedStartTime" class="entrytime" :id="'fromtimepicker_' + index" @blur="changeStartTime($event, index, 'from')">
              </div>
            </td>
            <td>
              <span>To</span>
            </td>
            <td>
              <div class="input-group table_timepickerfix" :id="'divShowtotimepicker_' + index" style="min-width:200px;">
                <!-- Assuming SetTimesPicker is a method of the Vue component -->
                <input type="text" v-model="item.ModifiedEndTime" class="entrytime" :id="'totimepicker_' + index" @blur="changeStartTime($event, index, 'to')">
              </div>
            </td>
            
          </tr>
        </tbody>
      </table>
    </div>
    <div class="row flex-row-reverse" v-if="data.length > 0">
      <div class="col-lg-6 text-right mt-2">
        <a href="javascript:;" class="btn btn-success text-white" id="btnSubmit" @click="submitForm" data-toggle="tooltip" title="Save"><i class="fa fa-save pr-2 text-white"></i>Save</a>
        <a href="javascript:;" class="btn btn-danger text-white " @click="resetForm" data-toggle="tooltip" title="Reset"><i class="fa fa-close pr-2 text-white"></i>Reset</a>
      </div>
      <div class="col-lg-6">
        <div class="py-2"></div>
      </div>
    </div>
  </section>
</template>
<script>
import DataService from '../../services/DataService'
import moment from 'moment';
export default {
  props: {
    data: {
      type: Array
    }
  },
  data() {
    return {

    }
  },
  created() {

  },
  watch: {
    data() {
      var vm = this;
      vm.data.forEach(x => {
        x.ModifiedStartTime = vm.FormatDateToTime(x.starT_TIME);
        x.ModifiedEndTime = vm.FormatDateToTime(x.enD_TIME);
      })
    }
  },
  methods: {
    // SetTimePickerDisable(isWeekend, ind) {
    //     if (isWeekend == "0") {
    //         $("div[data-target='#fromtimepicker_" + ind + "']").prop("disabled", true);
    //         $("div[data-target='#totimepicker_" + ind + "']").prop("disabled", true);
    //         $("#fromtimepicker_" + ind).addClass("disabled");
    //         $("#totimepicker_" + ind).addClass("disabled");
    //     }
    //     else {
    //         $("div[data-target='#fromtimepicker_" + ind + "']").prop("disabled", false);
    //         $("div[data-target='#totimepicker_" + ind + "']").prop("disabled", false);
    //         $("#fromtimepicker_" + ind).removeClass("disabled");
    //         $("#totimepicker_" + ind).removeClass("disabled");
    //     }
    // },
    FormatDateToTime(Getdate) {
      return moment(Getdate).format('LT');
    },
    submitForm(){
      var vm = this;
      vm.$emit('submitForm',vm.data);
    }
  }
}
</script>