<template lang="en">
    <section>
       <div>
        <div class="alert alert-info mb-0 mt-2 pl-0" role="alert">
                    <ol class="mb-0">
                        <li>{{$t('Noteforworkingweek')}}</li>
                        <li>{{$t('Noteforworkingweek1')}}</li>
                    </ol>
                </div>
            <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{$t('WorkingweekTitle')}}</span>
                    </div>
                </div>
            </div>
        </div> 
     <div class="border p-3">
            <dynamic-form lang="en" :schema="FormSchema" v-on:OnSubmit="submitForm"></dynamic-form>
            
        </div>
        <Week :data="data" @submitForm="submitForm"></Week>
    </section>
    </template>

<script>
import DataService from '../../services/DataService'
import Week from '../WorkingWeek/Week.vue'
export default {
    components:{
        Week
    },

    data() {

        return {
            shifttdata: [],
            FormSchema: [{
                layoutType: "double",
                Data: [
                    {
                        astype: "SelectField",
                        label: this.$t('ChooseShift'),
                        name: "Shift",
                        value: '',
                        placeholder: "",
                        validationRules: {
                            "required": false
                        },
                        config: {
                            options: [],
                            onChange:this.WeekDetail
                        }
                    },
                    {
                        astype: "DateField",
                        label: this.$t('ImplementDate'),
                        name: "ImplementDate",
                        value: "",
                        placeholder: "",
                        validationRules: { "required": false },
                    },


                ]
            },

            ],
            data:[]
        }
    },
    created: async function () {
        await this.GetAllShifts();
       
    },
    methods: {
        GetAllShifts: async function () {
            var vm = this;
            await DataService.GetAllShifts().then(response => {
                vm.Shifttdata = response.data;
                console.log("Shifttdata", vm.Shifttdata);
                if (vm.Shifttdata != null) {
                    vm.Shifttdata.forEach(function (item, index) {
                        (vm.FormSchema)[0].Data[0].config.options.push({
                            name: item.shift_name,
                            value: item.shift_id
                        })
                    })
                }
            })
        },
        WeekDetail:async function(){
            console.log("check",this.FormSchema[0].Data[0].value)
            var shiftId=this.FormSchema[0].Data[0].value
            var params = `shiftId=${shiftId}`
            await DataService.WeekDetail(params).then(response => {
                this.data=response.data
                console.log("res",this.data);
            })
        },
        submitForm(Weekdata){
            var vm = this;
            debugger
            var temparray = [];
            Weekdata.forEach(item=>{
                temparray.push({
                "WorkWeekId":item.worK_WEEK_ID,
                "DayId":item.daY_ID,
                "DayName":item.daY_NAME,
                "FromHour":item.froM_HOUR,
                "FromMin":item.froM_MIN,
                "ToHour":item.tO_HOUR,
                "ToMin":item.tO_MIN,
                "IsWorking":item.iS_WORKING,
                "FromTime":item.ModifiedStartTime,
                "ToTime":item.ModifiedEndTime,
                "ShiftId":vm.FormSchema[0].Data[0].value
                })
            })
            var json = {"WorkWeekModel": temparray};
            var params = `postString=${JSON.stringify(json)}&shifId=${vm.FormSchema[0].Data[0].value}&ImplementDate=${vm.$options.filters.formatDate(vm.FormSchema[0].Data[1].value)}`;
            DataService.AddUpdateWorkWeek(params).then(response=>{
                debugger
            }).catch(err=>{
                console.log(err);
            })
        }

    }
}
</script>